import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useRef, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useNavigate, useParams } from 'react-router-dom'

import { Box, Button, Card, Divider, Link, Typography } from '@mui/material'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

const ManyMemberMigrationCard = () => {
  const uploadInputRef = useRef<HTMLInputElement>(null)
  const params = useParams()
  const navigate = useNavigate()
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()
  const [isAttached, setIsAttached] = useState<boolean>(false)
  const [ref, setRef] = useState<HTMLInputElement>()

  const handleSubmit = async () => {
    // 아무 것도 선택안한 경우 return
    if (!ref || !ref.files || ref?.files?.length === 0) {
      enqueueSnackbar('파일을 선택하여 주십시오.', { variant: 'error' })
      return
    }

    const file = ref.files[0]
    const formData = new FormData()
    formData.append('file', file)

    try {
      const result = await axios.post(`${process.env.REACT_APP_APIHOST}/migration/upload/${params.id}`, formData, {
        headers: { 'content-Type': 'multipart/form-data', Authorization: authHeader() }
      })

      if (result.data.message !== 'ok') throw new Error()

      enqueueSnackbar('마이그레이션이 완료되었습니다.', { variant: 'success' })
      navigate(`/manage/${params.id}/member`)
    } catch (error) {
      if (axios.isAxiosError(error)) {
        enqueueSnackbar(error.response?.data?.message || '등록에 실패했습니다. 다시 시도해 주십시오.', {
          variant: 'error'
        })
      }
    }
  }

  return (
    <Card sx={{ padding: { xs: 2, sm: 2, md: 4 } }}>
      <Typography variant="h3" align="center" mb={3} color={PureLightTheme.colors.primary.main}>
        한번에 회원 추가
      </Typography>
      <Typography marginBottom={3} align="center">
        <Link href={process.env.REACT_APP_MIGRATION_EXAMPLE} target="_blank">
          여기
        </Link>
        를 클릭하시면 양식을 다운로드 받으실 수 있습니다. 양식에 맞추어 작성하신 후, 엑셀 파일을 업로드하세요.
      </Typography>

      <Divider />

      <Box mt={2} mb={5} marginTop={3}>
        <Box height={80}>
          {isAttached ? (
            <Box>
              <Button variant="outlined" fullWidth sx={{ height: '100%', display: 'block' }} color="success">
                첨부 완료
                <Typography variant="subtitle1">등록 버튼 클릭 시 바로 적용됩니다.</Typography>
              </Button>
            </Box>
          ) : (
            <Box>
              <input
                ref={uploadInputRef}
                type="file"
                accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                style={{ display: 'none' }}
                onChange={e => {
                  if (uploadInputRef.current) {
                    setRef(uploadInputRef.current)
                    setIsAttached(e.target?.files?.length ? e.target?.files?.length > 0 : false)
                  }
                }}
              />
              <Button
                onClick={() => uploadInputRef.current && uploadInputRef.current.click()}
                variant="outlined"
                fullWidth
                sx={{ height: '100%', display: 'block' }}
              >
                이 곳을 클릭하여 업로드하세요.
                <Typography variant="subtitle1">(.xlsx 확장자만 가능합니다.)</Typography>
              </Button>
            </Box>
          )}
        </Box>
      </Box>

      <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="등록" />
    </Card>
  )
}

export default ManyMemberMigrationCard
